<template>
  <v-card flat>
    <v-card-text>
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="data"
        item-key="no_order"
        :search="search"
        show-select
        class="elevation-1"
        :custom-sort="customSort"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-menu bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  :loading="loading"
                  :disabled="selected.length === 0 ? true : loading"
                  v-on="on"
                >
                  Actions
                </v-btn>
              </template>
              <v-list>
                <v-list-item @click="bailoutDialog()">
                  <v-list-item-title>
                    Move to Delivery
                  </v-list-item-title>
                </v-list-item>
                <!-- <v-list-item @click="postStatus(4)">
                  <v-list-item-title>
                    Move to Delivery Assigned
                  </v-list-item-title>
                </v-list-item> -->
                <v-list-item @click="postStatus(5)">
                  <v-list-item-title>
                    Move to Complete
                  </v-list-item-title>
                </v-list-item>
                <v-list-item @click="postStatus(6)">
                  <v-list-item-title>
                    Move to Canceled
                  </v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
            <v-btn
              class="ml-2"
              :disabled="selected.length == 0"
              @click="driverList"
            >
              Change Driver
            </v-btn>
            <v-divider
              class="mx-4"
              inset
              vertical
            />
            <v-text-field
              v-model="search"
              left
              class="mx-2"
              append-icon="mdi-magnify"
              label="Search"
              align-right
            />
          </v-toolbar>
        </template>

        <template v-slot:item.actions="{ item }">
          <v-btn
            icon
            small
            @click="detailOrder(item.no_order)"
          >
            <v-icon>mdi-eye</v-icon>
          </v-btn>

          <v-btn
            icon
            small
            @click="editOrder(item.no_order)"
          >
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
        </template>
        <template v-slot:item.delivery_fee="{ item }">
          {{ formatThousands(item.delivery_fee, { separator: ',' }) }}
        </template>
      </v-data-table>
    </v-card-text>
    <detail-order-dialog
      v-show="this.$store.state.dialog"
      :data="detail"
    />

    <edit-order-dialog
      v-show="this.$store.state.dialog_edit"
      :form="form"
    />
    <v-dialog
      v-model="dialog"
      max-width="400px"
    >
      <v-card>
        <v-card-title
          class="primary"
          style="color:white"
        >
          Assign Driver
        </v-card-title>

        <v-card-text
          class="mt-6"
          style="color:black"
        >
          <v-select
            v-model="selectedDriver"
            :items="driver_list"
            item-text="name"
            item-value="id"
            label="Choose an Driver"
            outlined
            clearable
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="driverAssign"
          >
            Submit
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog_bailout"
      max-width="400px"
    >
      <v-card>
        <v-card-title
          class="primary"
          style="color:white"
        >
          Dana Talangan
        </v-card-title>

        <v-card-text
          class="mt-6"
          style="color:black"
        >
          <v-radio-group
            v-model="bailout"
            row
          >
            <v-radio
              label="Dengan Talangan"
              value="1"
            />
            <v-radio
              label="Tanpa Talangan"
              value="2"
            />
          </v-radio-group>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="pickedup(4)"
          >
            Submit
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
  import axios from 'axios'
  import DetailOrderDialog from './DetailOrderDialog.vue'
  import EditOrderDialog from './EditOrderDialog.vue'
  import ChangeDriverDialog from './ChangeDriverDialog.vue'

  var formatThousands = require('format-thousands')
  export default {
    components: {
      DetailOrderDialog,
      EditOrderDialog,
      // eslint-disable-next-line vue/no-unused-components
      ChangeDriverDialog,
    },
    // eslint-disable-next-line vue/require-prop-types
    props: ['data'],
    data () {
      return {
        bailout: '1',
        formatThousands,
        driver_list: [],
        selectedDriver: [],
        selected: [],
        search: '',
        editDialog: false,
        dialog_bailout: false,
        dialog: false,
        sortable: false,
        driver: [],
        loading: false,
        detail: [],
        imgUrl: null,
        form: {
          order_id: 0,
          selectedUser: 0,
          selectedDistrict: 0,
          selectedMethod: 0,
          productName: null,
          weight: 0,
          volume: 0,
          price: 0,
          description: null,
          photo: null,
          receiverName: null,
          receiverAddress: null,
          receiverPhone: null,
          receiverPhone2: null,
          delivery_fee: 0,
          delivery_address_id: 0,
        },
        headers: [
          {
            text: 'No Order',
            value: 'no_order',
          },
          {
            text: 'Order Date',
            value: 'order_date',
          },
          {
            text: 'Client',
            value: 'client',
          },
          {
            text: 'Delivery Fee',
            value: 'delivery_fee',
          },
          {
            text: 'Driver',
            value: 'driver_name',
          },
          {
            text: 'Payment Method',
            value: 'payment_method',
          },
          {
            text: 'Actions',
            value: 'actions',
            sortable: false,
          },
        ],
      }
    },

    methods: {
      postStatus (status) {
        this.loading = true
        const id = this.selected
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            status: status,
          }),
        )
        axios.post('admin/status-exp', data).then(() => {
          this.refresh()
          this.selected = []
          this.loading = false
        })
      },
      bailoutDialog () {
        this.dialog_bailout = true
      },

      pickedup (status) {
        this.loading = true
        const id = this.selected
        const b = this.bailout
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            status: status,
            method: item.payment_method_id,
            bailout: b,
          }),
        )
        axios.post('admin/status-exp', data).then(() => {
          this.refresh()
          this.selected = []
          this.loading = false
          this.dialog_bailout = false
        })
      },

      refresh () {
        axios.get('admin/order-exp/pickup').then(res => {
          this.data = res.data.data
        })
      },

      detailOrder (no) {
        // eslint-disable-next-line camelcase
        const noOrder = no.replace(/#/g, '')
        console.log(noOrder)
        axios.get('admin/order/detail/' + noOrder).then(res => {
          this.detail = res.data.data[0]
          this.$store.commit('dialog', true)
        })
      },

      editOrder (no) {
        // eslint-disable-next-line camelcase
        const noOrder = no.replace(/#/g, '')
        console.log(noOrder)
        axios.get('admin/order/edit/' + noOrder).then(res => {
          const d = res.data.data[0]
          this.form.order_id = parseInt(d.id)
          this.form.selectedUser = d.user_id
          this.form.selectedDistrict = d.receiver_district
          this.form.selectedMethod = d.methods
          this.form.productName = d.product_name
          this.form.weight = d.weight
          this.form.volume = parseInt(d.volume)
          this.form.price = parseInt(d.price)
          this.form.description = d.description
          this.imgUrl = 'https://daengkurir.online/test/api/photo/' + d.photo
          this.form.photo = d.photo
          this.form.receiverName = d.receiver_name
          this.form.receiverAddress = d.description
          this.form.receiverPhone = d.receiver_phone
          this.form.delivery_fee = parseInt(d.delivery_fee)
          this.form.delivery_address_id = parseInt(d.delivery_address_id)
          this.$store.commit('dialog_edit', true)
        })
      },

      preview (e) {
        if (this.form.photo !== null) {
          this.form.photo = e
          this.imgUrl = URL.createObjectURL(this.form.photo)
        } else {
          this.imgUrl = null
        }
      },

      updateOrder () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('order_id', this.form.order_id)
        form.append('name', this.form.productName)
        form.append('weight', this.form.weight)
        form.append('volume', this.form.volume)
        form.append('price', this.form.price)
        form.append('description_order', this.form.description)
        form.append('photo', this.form.photo)
        form.append('receiver_name', this.form.receiverName)
        form.append('receiver_phone', this.form.receiverPhone)
        form.append('description_address', this.form.receiverAddress)
        form.append('district', this.form.selectedDistrict)
        form.append('payment_method', this.form.selectedMethod)
        form.append('user_id', this.form.selectedUser)
        form.append('delivery_fee', this.form.delivery_fee)
        form.append('delivery_address_id', this.form.delivery_address_id)
        axios
          .post('admin/order/update', form)
          .then(() => {
            this.$swal('Data Created Successfully', '', 'success')
            this.editDialog = false
            this.form.order_id = 0
            this.form.productName = null
            this.form.weight = 0
            this.form.volume = 0
            this.form.price = 0
            this.form.description = null
            this.form.photo = null
            this.form.receiverName = null
            this.form.receiverAddress = null
            this.form.selectedDistrict = null
            this.form.selectedMethod = null
            this.form.selectedUser = null
            this.form.delivery_address_id = 0
            this.form.delivery_fee = 0
          })
          .catch(error => {
            console.log(error)
          })
      },

      countFee () {
        this.form.delivery_fee = 0
        const w = parseInt(this.form.weight)
        const list = this.$store.state.del_fee_list
        list.forEach(item => {
          if (w > parseInt(item.from_weight) && w <= parseInt(item.to_weight)) {
            this.form.delivery_fee = parseInt(item.price)
          }
        })
      },

      driverList () {
        this.$swal.showLoading()
        const id = this.selected
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
          }),
        )
        axios.post('admin/change-driver-exp/filter', data).then(res => {
          this.driver_list = res.data.data
          this.dialog = true
          this.$swal.close()
        })
      },

      driverAssign () {
        const id = this.selected
        const d = this.selectedDriver
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            driver: d,
          }),
        )
        axios.post('admin/driver/change-driver-exp', data).then(() => {
          this.refresh()
          this.$swal('Data Updated Successfully', '', 'success')
          this.dialog = false
        })
      },

      customSort: function (items, index, isDesc) {
        items.sort((a, b) => {
          if (index[0] === 'order_date') {
            if (!isDesc[0]) {
              return new Date(b[index]) - new Date(a[index])
            } else {
              return new Date(a[index]) - new Date(b[index])
            }
          } else {
            if (typeof a[index] !== 'undefined') {
              if (!isDesc[0]) {
                return a[index].toLowerCase().localeCompare(b[index].toLowerCase())
              } else {
                return b[index].toLowerCase().localeCompare(a[index].toLowerCase())
              }
            }
          }
        })
        return items
      },
    },
  }
</script>
